import mpvuePicker from '@/components/mpvue-picker/mpvuePicker.vue';
import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
import cityData from '../../../components/w-picker/city-data/city.js';
export default {
	components: {
		mpvuePicker,
		mpvueCityPicker
	},
	data() {
		return {
			disabled: false,
			checked: true,	// 默认地址
			mulLinkageTwoPicker: cityData,
			cityPickerValueDefault: [1, 0, 1],  // 选择的省市区索引
			area: '',		// 选择的省市区
			name: '', // 收货人
			mobile: '',  // 手机号码
			id: '',  // 修改收货地址id
			detail: '',		// 详细地址
		};
	},
	components: {},
	computed: {},
	methods: {
		check(){
			this.checked = !this.checked
		},
		// 三级联动选择
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
		},
		onConfirm(e) {
			this.area = e.label
			this.cityPickerValueDefault = e.value
		},
		submit(){
			let data = {
				id: this.id,
				name: this.name,
				mobile: this.mobile,
				addr: this.detail,
				area: this.area,
				areaids: this.cityPickerValueDefault,
				is_default: this.checked?1:2
			}
			if(!this.name || !this.mobile || !this.area || !this.detail){
				uni.showToast({
					title: '带星号为必填',
					icon: 'none'
				})
				return
			}
			this.disabled = true
			this.$store.dispatch('setAdress',data).then(res=>{
				this.disabled = false
				uni.showToast({
					title: this.id!=''?'修改成功':'添加成功',
					success: ()=>{
						setTimeout(() => {
							uni.navigateBack({
								delta: 1
							})
						}, 1500);
					}
				})	
				
			}).catch(err=>{
				this.disabled =false
			})
		},
		getAddress(){
			this.$store.dispatch('getAddressNow',{
				id: this.id
			}).then(res=>{
				this.name = res.addr.name
				this.mobile = res.addr.mobile
				this.checked = res.addr.is_default==1?true:false
				this.cityPickerValueDefault = res.addr.areaids
				this.detail = res.addr.addr
				this.area = res.addr.area
			})
		}
	},
	onLoad(e){
		if(e.id!=undefined){
			this.id = e.id
			this.getAddress()
		}
	}
}
